import React, { useState, useEffect } from "react";
import api from "../../Services/api";
import Cookies from "js-cookie";
import DataTable from "react-data-table-component";

import "./styles.css";

function RegistrosAtendimentos() {
  const [response, setResponse] = useState("");
  const [isAdmin] = useState(Cookies.get('isAdmin'))

  useEffect(() => {
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${Cookies.get("x-acess-token")}`,
      user_id: Cookies.get("userId"),
    };

    const pathUrl = () => {
      return isAdmin === "true" ? '/attendances' : '/attendancesId'
    }

    api
      .get(pathUrl(), { headers: headers })
      .then((response) => {
        setResponse(response.data);
      })
      .catch((err) => {
        setResponse(err.response.data.message);
      });

    
  }, [isAdmin]);

  const customStyles = {
    rows: {
      style: {
        minHeight: "48px",
      },
    },
    headCells: {
      style: {
        paddingLeft: "8px",
        paddingRight: "8px",
      },
    },
    cells: {
      style: {
        paddingLeft: "8px",
        paddingRight: "8px",
      },
    },
  };

  const columns = [
    {
      name: "Profissional",
      selector: "profissional",
      sortable: false,
    },
    {
      name: "Data",
      selector: "data",
      sortable: false,
    },
    {
      name: "Paciente",
      selector: "paciente",
      sortable: false,
    },
    {
      name: "Paciente Criança ?",
      selector: "isCrianca",
      sortable: false,
    },
    {
      name: "Observações",
      selector: "observacoes",
      sortable: false,
      wrap: true,
    },
  ];

  return (
    <div id="tableWrap">
      <DataTable
        columns={columns}
        data={response ? response : []}
        customStyles={customStyles}
        striped={true}
        pagination={true}
        paginationPerPage={30}
      />
    </div>
  );
}

export default RegistrosAtendimentos;
