import React, { Suspense } from "react";
import { BrowserRouter } from "react-router-dom";
import FormAtendimentos from "./Components/FormAtendimentos/FormAtendimentos";
import FormDesistencias from "./Components/FormDesistencias/FormDesistencias";
import Navbar from "./Components/Navbar/Navbar";
import { PrivateRoute } from "./Services/PrivateRoute";
import { RedirectRoute } from "./Services/RedirectRoute";
import Login from "./Components/Login/Login";
import isAuthenticated from "./Services/isAuthenticated";
import RegistrosAtendimentos from "./Components/RegistrosAtendimentos/RegistrosAtendimentos";
import RegistrosDesistencias from "./Components/RegistrosDesistencias/RegistrosDesistencias";
import CadastroUsuario from "./Components/CadastroUsuarios/CadastroUsuarios";

function Routes() {
  return isAuthenticated() ? (
    <>
      <BrowserRouter>
        <Navbar />
        <Suspense fallback={""}>
          <PrivateRoute
            path="/atendimentos"
            component={() => <FormAtendimentos />}
          />
          <PrivateRoute
            path="/desistencias"
            component={() => <FormDesistencias />}
          />
          <PrivateRoute
            path="/registros/atendimentos"
            component={() => <RegistrosAtendimentos />}
          />
          <PrivateRoute
            path="/registros/desistencias"
            component={() => <RegistrosDesistencias />}
          />
          <PrivateRoute
            path="/usuarios/criar"
            component={() => <CadastroUsuario />}
          />
        </Suspense>
      </BrowserRouter>
    </>
  ) : (
    <BrowserRouter>
      <Suspense fallback={""}>
        <RedirectRoute path="*" component={() => <Login />} />
      </Suspense>
    </BrowserRouter>
  );
}

export default Routes;
