import React from 'react'
import {Route, Redirect} from 'react-router-dom'

export const RedirectRoute = ({ component: Component, ...rest }) => (
  
  <Route
    {...rest}
    render={props =>
      window.location.pathname === '/login' ? (
        <Component {...props} />
      ) : (
          <Redirect to={{ pathname: '/login', state: { from: props.location } }} />
        )
    }
  />
)