import React, { useState, useEffect } from 'react'
import Cookies from 'js-cookie'
import api from '../../Services/api'
import './styles.css'

function Login() {

  useEffect(() => {
    const hasToken = Cookies.get('x-acess-token')
    const hasUserId = Cookies.get('userId')

    if (hasToken && hasUserId) {
      window.location = '/home'
    }
  }, [])

  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [response, setResponse] = useState('');

  function handleSubmit(e) {
    e.preventDefault();

    const inOneHour = new Date(new Date ().getTime() + 1 * 60 * 60 * 1000)

    api.post('/sessions', {
      username,
      password
    }).then(response => {
      Cookies.set('x-acess-token', response.data.token, {expires: inOneHour})
      Cookies.set('userId', response.data.user._id, {expires: inOneHour})
      Cookies.set('fullname', response.data.user.fullname)
      Cookies.set('username', response.data.user.username)
      Cookies.set('userId', response.data.user._id)
      Cookies.set('isAdmin' ,response.data.user.isAdministrator)


      window.location = '/atendimentos'
    }).catch((err) => {
      setResponse(err.response.data.message)
    })
  }

  return (
    <>
      <div className="wrapper fadeInDown">
        <div id="formContent">
          <div className="fadeIn first">
            <h1>Login</h1>
          </div>

          <form onSubmit={handleSubmit}>
            <input type="text" onChange={e => { setUsername(e.target.value) }} id="login" className="fadeIn second" name="login" placeholder="Usuário" />
            <input type="password" onChange={e => { setPassword(e.target.value) }} id="password" className="fadeIn third" name="login" placeholder="Senha" />
            <input type="submit" className="fadeIn fourth" value="Entrar" />
            <p>{response}</p>
          </form>

        </div>
      </div>
    </>
  )
}

export default Login

