import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import Cookies from "js-cookie";
import Logout from "../Logout/Logout";

import "./styles.css";

function Navbar() {
  const [isAdmin] = useState(Cookies.get("isAdmin"));

  const returnHtmlIfIsAdmin = () => {
    if (isAdmin === "true") {
      return <NavLink to="/usuarios/criar">Cadastrar Profissional</NavLink>;
    }
  };

  return (
    <>
      <div id="navbar">
        <NavLink to="/atendimentos">Atendimentos</NavLink>
        <NavLink to="/desistencias">Desistências</NavLink>
        <NavLink to="/registros/atendimentos">Registros Atendimentos</NavLink>
        <NavLink to="/registros/desistencias">Registros Desistências</NavLink>
        {returnHtmlIfIsAdmin()}
        <a href="/login" onClick={Logout}>
          Sair
        </a>
      </div>
    </>
  );
}

export default Navbar;
