import React, { useState } from "react"
import api from '../../Services/api'
import Cookies from 'js-cookie'

import './styles.css'

function FormDesistencias() {
  const [dia, setDia] = useState("")
  const [paciente, setPaciente] = useState("")
  const [isCrianca, setIsCrianca] = useState("Sim")
  const [observacoes, setObservacoes] = useState("")
  const [response, setResponse] = useState("");
  const [responseStatus, setResponseStatus] = useState("failed");

  const nome = Cookies.get('fullname')

  function hasResponse() {
    if (response) {
      return response;
    }
    return;
  }

  const resetStates = () => {
    setDia("");
    setPaciente("");
    setObservacoes("");
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${Cookies.get("x-acess-token")}`,
      'user_id': Cookies.get('userId')
    };

    api
      .post("/surrenders", {
        profissional: nome,
        data: dia,
        paciente: paciente,
        isCrianca: isCrianca,
        observacoes: observacoes,
      }, { headers: headers })
      .then((response) => {
        setResponse(response.data);
        if (response.status === 200) {
          setResponseStatus("sucess");
          resetStates();
        }
      })
      .catch((err) => {
        console.log(err.response.data)
        setResponse(err.response.data);
      });
  };

  return (
    <div id="form-desistencias">
      <h2>Formulário de Desistência</h2>
      <form onSubmit={handleSubmit}>
        <label>
          Nome do Profissional:
          <input
            type="text"
            value={nome}
            readOnly={true}
          />
        </label>

        <label>
          Data da Tentativa de Contato:
          <input
            type="date"
            value={dia}
            onChange={e => setDia(e.target.value)}
          />
        </label>

        <label>
          Nome do Paciente:
          <input
            type="text"
            value={paciente}
            onChange={e => setPaciente(e.target.value)}
          />
        </label>

        <label>
          Paciente é criança ?
          <select
            onChange={(e) => setIsCrianca(e.target.value)}
            value={isCrianca}
          >
            <option value="sim">Sim</option>
            <option value="Não">Não</option>
          </select>
        </label>

        <label>
          Observações:
          <textarea
            value={observacoes}
            onChange={e => setObservacoes(e.target.value)}
            rows="3"
            placeholder="Apenas preencha esse campo se tiver alguma observação sobre a desistência"
          />
        </label>

        <input type="submit" value="Enviar" />

        <p className={responseStatus}>{hasResponse()}</p>
      </form>
    </div>
  );
}

export default FormDesistencias;
