import React, { useState } from "react";
import Cookies from 'js-cookie'
import api from "../../Services/api";
import "./styles.css";

function CadastroUsuario() {
  const [fullname, setFullname] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [isAdministrator, setIsAdministrator] = useState(false);
  const [response, setResponse] = useState("");
  const [responseStatus, setResponseStatus] = useState("failed")

  function hasResponse() {
    if (response) {
      return response;
    }
    return;
  }

  const resetStates = () => {
    setFullname('')
    setUsername('')
    setPassword('')
    setIsAdministrator(false)
  };

  function handleSubmit(e) {
    e.preventDefault();

    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${Cookies.get("x-acess-token")}`,
    };

    api
      .post("/users", {
        fullname,
        username,
        password,
        isAdministrator
      }, { headers: headers })
      .then((response) => {
        setResponse(response.data);
        if (response.status === 200) {
          setResponseStatus("sucess");
          resetStates();
        }
      })
      .catch((err) => {
        console.log(err.response.data);
        setResponse(err.response.data);
      });
  }

  return (
    <>
      <div className="wrapper fadeInDown">
        <div id="formContent">
          <div className="fadeIn first">
            <h1>Cadastro de Profissional</h1>
          </div>

          <form onSubmit={handleSubmit}>
            <input
              type="text"
              onChange={(e) => {
                setFullname(e.target.value);
              }}
              id="fullname"
              className="fadeIn second"
              name="fullname"
              placeholder="Nome Completo"
            />
            <input
              type="text"
              onChange={(e) => {
                setUsername(e.target.value);
              }}
              id="username"
              className="fadeIn second"
              name="username"
              placeholder="Nome de Usuário (login)"
            />
            <input
              type="text"
              onChange={(e) => {
                setPassword(e.target.value);
              }}
              id="password"
              className="fadeIn third"
              name="password"
              placeholder="Senha"
            />
            <label className="fadeIn third">
              Profissional é administrador?
              <select
                className="isAdmin"
                onChange={(e) => setIsAdministrator(e.target.value)}
                value={isAdministrator}
              >
                <option value={false}>Não</option>
                <option value={true}>Sim</option>
              </select>
            </label>
            <input type="submit" className="fadeIn fourth" value="Cadastrar" />
            <p className={responseStatus}>{hasResponse()}</p>
          </form>
        </div>
      </div>
    </>
  );
}

export default CadastroUsuario;
